.auth-wrapper-summary {
  display: block;
  justify-content: center;
  flex-direction: row;
  text-align: left;
  margin-top: 0;
  padding-top: 0%;
}

/* @media (min-width: 768px) {
  .auth-wrapper-summary {
    margin-top: 20px;
  }
} */

.auth-inner-summary {
  width: 100%;
  max-width: 700px;
  margin: auto;
  background: #fbfcfc; /* #ccd1d1; */
  /* box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2); */
  padding: 1vh 1vw;
  border-radius: 5px;
  transition: all 0.3s;
}

.autoWidth {
  max-width: 500px;
  -webkit-transition: max-width 0.5s ease;
  transition: max-width 0.5s ease;
}

