/* 
* Start user theme 
*/
:root {
  --primary: #6c757d !important;
  --light: #f8f9fa !important;
  --white: #fff !important;
  --dark: #6c757d !important;
  --gray-dark: #343a40 !important;
  --active: #f0f1f2 !important;
  --disabled: #e9ecef !important;
  --danger: #dc3545 !important;
  --info: #17a2b8 !important;
}

* {
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  display: flex;
}

h1,
h2,
h3,
h4,
h5,
h6,
label,
span {
  font-weight: 500;
  font-family: "Fira Sans", sans-serif;
}

body,
html,
.App,
#root,
.auth-wrapper {
  width: 100%;
  height: 100%;
}

.navbar-light {
  background-color: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}

@media (min-width: 768px) {
  .auth-wrapper {
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: left;
    padding-bottom: 5%;
  }
}

.auth-inner {
  max-width: 500px;
  width: 100%;
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 3px 10px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all 0.3s;
}

.auth-wrapper .form-control:focus {
  border-color: #6c757d !important;
  border-color: var(--primary) !important;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.other-options,
.other-options a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}

